<template>
  <div class="cart-container">
    <div class="top">
      <div class="top-head">
        <div class="title">Shopping Cart</div>
        <div class="operation">Operation</div>
      </div>
      <div class="top-footer">
        <div class="goods-count"> {{$store.state.cartlist.length}} Babies In Total</div>
        <div class="receiving-address">Receiving Address：dddddddddafdadafafaf;afa;dafjadjfafd</div>
      </div>
    </div>
    <div class="cart-content">
      <div class="cart-item" v-for="(item,index) in $store.state.cartlist" :key="index">
        <div class="cart-item-content">
          <input type="checkbox" name="cardlist" :value="JSON.stringify(item)" @click="checkGoods"/>
          <img
            :src="item.goods_img"
          />
          <div class="goods-brief">
            <div class="goods-name">{{item.goods_name}}</div>
            <div class="goods-specifications">{{item.goods_specifications}}</div>
          </div>
        </div>
        <div class="cart-item-price">
          <div class="goods-price">${{item.goods_sell_price}}</div>
          <div class="goods-count">
            <div @click="countReduce(item.goods_id,item.cart_count)">-</div>
            <div>{{item.cart_count}}</div>
            <div @click="countAdd(item.goods_id)">+</div>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // goodsCount:1
      checkedGoods:[]
    };
  },
  created() {
    // let isLogin=this.getLoginStatus()
    // this.goLoginRegister(isLogin)
      
  },
  mounted(){
    this.getCartlist()
  },
  methods: {
    goLoginRegister(status) {
      if (status == false) {
        this.$router.push({ path: "/home/loginregister" });
      } else {
        return;
      }
    },
    getLoginStatus() {
      let isLogin = window.localStorage.getItem("isLogin") || false;
      return isLogin;
    },
    countReduce(id,count){
      if(count < 2){return}
      this.$store.commit('decrement',id)
    },
    countAdd(id){
      // this.goodsCount++;
      this.$store.commit('increment',id)
    },
    async getCartlist(){
      const {data} =await this.$http.get(this.baseUrl+'/api/goods/cartlist')
      window.localStorage.setItem('cartlist',JSON.stringify(data.data))
      // console.log(data.data)
    },
    checkGoods(e){
      let value=JSON.parse(e.target.defaultValue)
      if(e.target.checked){
        this.checkedGoods.push( value )
      }else{
        let index=this.checkedGoods.findIndex(item=>{
          return item.goods_id == value.goods_id
        })
        this.checkedGoods.splice(index,1)
        
      }
      // console.log(this.checkedGoods)
    }

  }
};
</script>

<style scoped lang="scss">
.cart-container {
  height: 100%;
  overflow: auto;
  width: 100%;
  position: relative;
}
.top {
  background-color: #000;
  height: 1.5rem;
  color: #fff;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  .top-head {
    display: flex;
    height: 0.8rem;
    line-height: 0.8rem;
    padding: 10px;
    .title {
      font-size: 0.4rem;
    }
    .operation {
      font-size: 0.3rem;
      font-weight: bolder;
      flex: 1;
      text-align: right;
    }
  }
  .top-footer {
    font-size: 0.25rem;
    display: flex;
    padding: 10px;
    .receiving-address {
      margin-left: 20px;
      width: 6rem;
      text-align: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
.cart-content {
  position: relative;
  top: 1.5rem;
  overflow: auto;
  height:calc(100% - 2.7rem);
  .cart-item {
    height: 4rem;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 10px #ccc;
    width: 95%;
    margin: 10px 10px 0px 10px;
    border-radius: 10px;
    padding: 10px;
    .cart-item-content {
      display: flex;
      align-items: center;
      justify-content: center;

      input {
        height: 0.5rem;
        width: 0.5rem;
        border-radius: 0.25rem;
        margin: 0px 10px;
      }
      img {
        height: 2.5rem;
        width: 2.5rem;
        margin: 0 20px;
      }
      .goods-brief {
        flex: 1;
        font-size: 0.3rem;
        height: 2.5rem;
        text-align: left;
        .goods-name {
          height: 1.25rem;
          width: 95%;
          padding: 10px;
        }
        .goods-specifications {
          background-color: #eee;
          height: 1.25rem;
          width: 95%;
          padding: 10px;
        }
      }
    }
    .cart-item-price{
      display: flex;
      align-items: center;
      height: 1rem;
      margin-top:10px;
      .goods-price{
        width: 60%;
        text-align: right;
        font-size: 0.4rem;
        color: #f40;
      }
      .goods-count{
        display: flex;
        flex: 1;
        justify-content: center;
        font-size: 0.35rem;
        div:nth-of-type(odd){
          border:1px solid #ccc;
          background-color: #eee;
          padding: 10px 20px;
        }
        div:nth-of-type(even){
          border-top:1px solid #ccc;
          border-bottom:1px solid #ccc;
          
          width: 1rem;
          padding: 10px 0;
        }
      }
    }
  }
}
.bottom {
  border-top: 1px solid #ccc;
  height: 1.2rem;
  width: 100%;
  position: absolute;
  bottom: 0;
}
</style>